@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:100,300,400&display=swap);
:root{--color-main: #f2f2f1;--color-main-light: rgba(242, 242, 241, 0.5);--color-opposite: #48484a}*,*:before,*:after{box-sizing:border-box;margin:0;padding:0}.o-button,.o-input{-webkit-appearance:none;-moz-appearance:none;appearance:none;height:2rem;border:none;border-radius:10px;background-color:#48484a;background-color:var(--color-opposite);font:inherit;text-align:center}html{font-size:14px}body{font-size:1rem;font-weight:300;font-family:'Josefin Sans', sans-serif;font-weight:300;letter-spacing:0.06rem;line-height:1.4;color:#f2f2f1;color:var(--color-main)}h1{font-size:2.441rem;font-weight:100;text-align:center}h2{font-size:1rem;font-weight:400;text-align:left;color:#ec6e4c}h2 span{font-weight:400;text-align:center;text-transform:uppercase;color:#f2f2f1;color:var(--color-main)}a{font:inherit;text-decoration:none;color:inherit;-webkit-transition:color 300ms ease-in-out;transition:color 300ms ease-in-out}a:hover{color:rgba(236,110,76,0.8);text-decoration:underline}li{list-style:none}@media (min-width: 340px){html{font-size:15px}}@media (min-width: 768px){html{font-size:16px}}@media (min-width: 1024px){html{font-size:18px}}html{background-color:#48484a}.l-app{position:relative;width:100%;min-height:100vh;padding:10px;background-color:#48484a;background-color:var(--color-opposite);-webkit-transition:background-color 0.5s ease-in-out;transition:background-color 0.5s ease-in-out}@media (min-width: 340px){.l-app{padding:20px}}@media (min-width: 768px){.l-app{padding:40px}}@media (min-width: 1024px){.l-app{padding:60px}}@media (min-width: 1440px){.l-app{padding:80px}}.l-header{display:grid;grid-template-columns:1fr 2rem 1fr;grid-template-rows:auto auto;grid-row-gap:10px;align-items:center;justify-items:center;margin:0 auto 2rem;max-width:1000px}.l-header__title{grid-column:span 3}.l-section{width:100%;margin:0 auto;display:flex;align-items:center;justify-content:center;max-width:800px;flex-direction:column;margin:10px auto;padding:1rem 0}.l-section--forecast{max-width:1400px}.l-section__header{margin-bottom:1rem}.l-section__header--bold{display:block}@media (min-width: 768px){.l-section__header--bold{display:inline-block}}.o-spinner{padding:1rem 1rem 2rem;margin:0 auto;color:#ec6e4c;text-align:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;background:transparent;border-radius:1rem}.o-spinner__message{color:#f2f2f1;color:var(--color-main);margin-bottom:2rem}.o-spinner__spinner{width:3rem;height:3rem;border-radius:50%;position:relative;opacity:1;margin:0 auto}.o-spinner__spinner:before,.o-spinner__spinner:after{content:'';border:2px solid;border-radius:50%;width:100%;height:100%;position:absolute;left:0px}.o-spinner__spinner:before{-webkit-transform:scale(1, 1);transform:scale(1, 1);opacity:1;-webkit-animation:spWaveBe 0.6s infinite linear;animation:spWaveBe 0.6s infinite linear}.o-spinner__spinner:after{-webkit-transform:scale(0, 0);transform:scale(0, 0);opacity:0;-webkit-animation:spWaveAf 0.6s infinite linear;animation:spWaveAf 0.6s infinite linear}@-webkit-keyframes spWaveAf{from{-webkit-transform:scale(0.5, 0.5);opacity:0}to{-webkit-transform:scale(1, 1);opacity:1}}@keyframes spWaveAf{from{-webkit-transform:scale(0.5, 0.5);transform:scale(0.5, 0.5);opacity:0}to{-webkit-transform:scale(1, 1);transform:scale(1, 1);opacity:1}}@-webkit-keyframes spWaveBe{from{-webkit-transform:scale(1, 1);opacity:1}to{-webkit-transform:scale(1.5, 1.5);opacity:0}}@keyframes spWaveBe{from{-webkit-transform:scale(1, 1);opacity:1}to{-webkit-transform:scale(1.5, 1.5);opacity:0}}.o-button{padding:0 10px;color:#f2f2f1;color:var(--color-main);-webkit-transition:color 300ms ease-in-out;transition:color 300ms ease-in-out;box-shadow:2px 2px 5px rgba(0,0,0,0.5),-2px -2px 5px rgba(255,255,255,0.1)}.o-button:hover{color:#ec6e4c;cursor:pointer}.o-button:active{box-shadow:inset 2px 2px 5px rgba(0,0,0,0.5),inset -2px -2px 5px rgba(255,255,255,0.1)}.o-button--nav{display:none;width:3rem;height:3rem;padding:0.5rem;margin:0 1rem;z-index:100}.o-button--nav__icon{width:100%;height:100%;fill:#f2f2f1;fill:var(--color-main);opacity:.7;-webkit-transition:fill 0.3s ease-in-out;transition:fill 0.3s ease-in-out}.o-button--nav:hover .o-button--nav__icon{fill:#ec6e4c}@media (max-width: 768px){.o-button--nav{display:block}}.o-input{box-shadow:inset 2px 2px 5px rgba(0,0,0,0.5),inset -2px -2px 5px rgba(255,255,255,0.1);width:80%;max-width:300px;margin:10px 0;color:inherit}@media (min-width: 768px){.o-input{width:60%}}.o-form{width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}@media (min-width: 768px){.o-form{flex-direction:row;justify-content:space-evenly}}.c-nav--main{justify-self:end}.c-nav--settings{justify-self:start}.c-nav--is-expanded{position:absolute;top:0;left:0;width:100vw;height:100vh;z-index:10;padding:10px;background:#48484a;background:var(--color-opposite)}.c-nav--is-expanded .c-nav__list{height:60%;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}.c-nav__list{display:flex}.c-nav__list--nav{justify-content:flex-end}.c-nav__list--settings{justify-content:flex-start}.c-nav__item{margin:0 0.8rem;color:#f2f2f1;color:var(--color-main)}.c-nav__item--is-active{color:#ec6e4c;font-weight:400}@media (max-width: 768px){.c-nav__list{display:none}}.c-icon--header{fill:#ec6e4c;width:2rem;height:2rem}.c-switch+.c-switch{margin-left:10px}.c-switch__name{text-align:center}.c-switch__container{display:flex;justify-content:center;align-items:center}.c-switch__input{opacity:0}.c-switch__input:focus+.c-switch__label{outline:auto}.c-switch__input:checked+.c-switch__label .c-switch__icon{fill:#ec6e4c;-webkit-transform:scale(1.5);transform:scale(1.5)}.c-switch__input:checked+.c-switch__label .c-switch__flag{font-size:1.5rem}.c-switch__label{position:relative;left:-12px;display:flex;flex-direction:row;flex-wrap:nowrap;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.c-switch__label:hover{cursor:pointer}.c-switch__icon{width:1.5rem;fill:#f2f2f1;fill:var(--color-main);-webkit-transition:-webkit-transform 300ms;transition:-webkit-transform 300ms;transition:transform 300ms;transition:transform 300ms, -webkit-transform 300ms}.c-switch__flag{font-size:1rem}.c-weather{width:100%;margin:0 auto;display:flex;align-items:center;justify-content:center;justify-content:space-evenly;width:100%}.c-weather--forecast{flex-wrap:wrap}.c-weather__icon{background-color:rgba(242, 242, 241, 0.5);background-color:var(--color-main-light);width:100px;height:100px;border-radius:50%}.c-weather__details{display:flex;flex-direction:column;justify-content:space-between;align-items:center;min-height:120px}.c-weather__details--forecast{margin:30px 20px;min-width:200px}.c-weather__details>*{margin-bottom:10px}.c-weather__date{text-align:center}.c-weather__description{font-weight:400;text-align:center}.c-weather__temperature{font-size:1.953rem}.c-weather__full{padding-top:1rem 0}.c-weather__item:last-of-type{margin-bottom:10px}.c-about__text{align-self:flex-start;margin-bottom:20px}.c-about__link{display:block;text-decoration:underline;margin:10px 0}

